@import "_header";
@import "_footer";
.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
}

.container {
    width: 1300px;
    max-width: 100%;
    margin: 0 auto;
    padding: 0 30px;
    @media screen and (max-width: 1024px) {
        padding: 0 80px;
    }
    @media screen and (max-width: 800px) {
        padding: 0 20px;
    }
    @media screen and (max-width: 380px) {
        padding: 0 10px;
    }
}